<template>
  <div class="p-5 rounded-[10px] bg-white flex items-center justify-center">
    <notification-detail-card v-bind="data?.juvenile" :sender_center="data?.sender_center" :rejection_reason="data?.rejection_reason" :status="data?.status" @reject-juvenile="rejectJuvenile" />
    <ModalComponent ref="rejectModal" modalClass="!w-auto" modal-position="center">
      <template #slotBody>
        <RejectNotificationModalContent @cancel="closeModal" @reject-juvenile="rejectRequestJuvenile" />
      </template>
    </ModalComponent>
  </div>
</template>

<script setup>
// ######## imports ######
import {ref, watch} from 'vue';
import {useRoute, useRouter} from 'vue-router';

import NotificationsService from '@/services/notifications.service';
import useAlert from '@/composables/useAlert';

import NotificationDetailCard from '../components/Notification/NotificationDetailCard.vue';
import ModalComponent from '../components/Common/Modal/ModalComponent.vue';
import RejectNotificationModalContent from '../components/Notification/RejectNotificationModalContent.vue';

const route = useRoute();
const router = useRouter();

const {getNotificationDetails, rejectNotification} = NotificationsService;

const data = ref(null);
const rejectModal = ref();
const {alertIt} = useAlert();
// ####### get data #####

const getData = async () => {
  const {data: dataNotification} = await getNotificationDetails(route.params?.id);
  data.value = dataNotification;
};

const rejectJuvenile = () => {
  rejectModal.value.openModal();
};

const closeModal = () => {
  rejectModal.value.closeModal();
};

const rejectRequestJuvenile = async (rejection_reason) => {
  const {data} = await rejectNotification(route.params.id, {rejection_reason});
  console.log(data);
  alertIt(data?.message);
  router.push('/');
};

// getData();

watch(() => route.params.id, getData, {immediate: true});
</script>

