import axios from './api/axios';

const NotificationsService = {
  async getNotificationsList() {
    return await axios.get('api/notifications/');
  },
  async getNotificationDetails(id) {
    return await axios.get(`api/notifications/${id}/`);
  },
  async getNotificationAccept(id) {
    return await axios.post(`api/notifications/${id}/accept_juvenile/`);
  },
  async completeNotification(id) {
    return await axios.get(`api/notifications/${id}/complete_notification/`);
  },
  async rejectNotification(id, data) {
    return await axios.post(`api/notifications/${id}/reject_juvenile/`, data);
  },
};
export default NotificationsService;
