<template>
  <div class="max-w-4xl flex items-center justify-center flex-col gap-6">
    <div class="h-56 overflow-hidden rounded-md">
      <img :src="photo" alt="cover_photo" class="w-full h-full object-cover" />
    </div>
    <ul class="w-full">
      <notification-detail-list label="Ismi:" :value="first_name" class-name="bg-[#F5F5F5]" />
      <notification-detail-list label="Familiyasi:" :value="last_name" />
      <notification-detail-list label="Sharifi:" :value="father_name" class-name="bg-[#F5F5F5]" />
      <notification-detail-list label="Jinsi:" :value="gender" />
      <notification-detail-list label="Tug’ilgan sanasi:" :value="birth_date" class-name="bg-[#F5F5F5]" />
      <notification-detail-list label="Tug’ilgan joyi:" :value="birth_region" />
      <notification-detail-list label="Manzi:" :value="address" class-name="bg-[#F5F5F5]" />
      <notification-detail-list label="Qayerdan:" :value="sender_center" />
      <notification-detail-list v-if="rejection_reason" label="Qayerdan:" :value="rejection_reason" class-name="bg-[#F5F5F5]" />
    </ul>
    <div class="flex gap-4 justify-center align-center" v-if="status?.id === 1">
      <button @click="acceptJuvenile" class="bg-[#4785FE] px-[30px] py-[8px] rounded-[5px] text-lg text-white">Qabul qilish</button>
      <button @click="$emit('reject-juvenile')" class="bg-[#FF4444] px-[30px] py-[8px] rounded-[5px] text-lg text-white">Rad etish</button>
    </div>
    <div class="flex gap-4 justify-center align-center" v-else>
      <button @click="complete" class="bg-[#4785FE] px-[30px] py-[8px] rounded-[5px] text-lg text-white">Xabarnomani yakunlash</button>
    </div>
  </div>
</template>

<script setup>
import {defineProps, defineEmits} from 'vue';
import {useRoute, useRouter} from 'vue-router';

import NotificationsService from '@/services/notifications.service';
import useAlert from '@/composables/useAlert';

import NotificationDetailList from './NotificationDetailList.vue';

const route = useRoute();
const router = useRouter();
const {alertIt} = useAlert();

const {getNotificationAccept, completeNotification} = NotificationsService;

defineProps(['photo', 'first_name', 'last_name', 'father_name', 'gender', 'birth_date', 'birth_region', 'address', 'center_opinion_file', 'sender_center', 'rejection_reason', 'status']);

const $emit = defineEmits(['reject-juvenile']);

const acceptJuvenile = async () => {
  const {data} = await getNotificationAccept(route.params.id);
  console.log(data);
  alertIt(data?.message);
  router.push('/');
};
const complete = async () => {
  const {data} = await completeNotification(route.params.id);
  console.log(data);
  alertIt(data?.message);
  router.push('/');
};
</script>

